@import "ScssHelpers/helpers-all.scss";

.featured_recipe {
	// padding-top: 5.2rem;
	// padding-bottom: 6.6rem;
}

.featured_recipe__wrap {
	@include bp($point_2, min) {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
}

.featured_recipe__col {
	&:nth-child(2n - 1) {

		@include bp($point_2, min) {
			flex-shrink: 0;
			width: 62.2%;

			padding-right: 5.8rem;
		}

		@include bp($point_3) {
			position: relative;

			width: 100%;
			margin-bottom: 3.3rem;
		}
	}

	&:nth-child(2n - 2) {
		padding-top: 1.2rem;

		@include bp($point_2, min) {
			margin-right: -2.4rem;
			margin-left: auto;
		}
	}
}

.featured_recipe__title {
	@include h3;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.3rem;
		}
	}

	.featured_recipe & {
		@include bp($point_2, min) {
			letter-spacing: -0.016em;
		}

		@include bp($point_3) {
			letter-spacing: -0.03em;
		}
	} 
}

.featured_recipe__slider {
	position: relative;
}

.featured_recipe__heading {
	@include h4;

	&:not(:last-child) {		
		@include bp($point_2, min) {
			margin-bottom: 2.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.7rem;
		}
	}

	@include bp($point_2, min) {
		margin-left: 3.8rem;

		letter-spacing: -0.014em;
	}

	@include bp($point_4 - 1) {
		letter-spacing: -0.03em;
		font-size: 2.2rem;
	}

	
}

.featured_recipe__list {

	background-color: #fff6f1;

	@include bp($point_2, min) {
		padding: 4rem 2rem 4.8rem 4rem;
	}

	@include bp($point_3) {
		padding: 2.4rem 2rem 4.8rem 2.4rem;
	}
}

.featured_recipe__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.4rem;
		}
	}
}

.featured_recipe__button {
	position: absolute;
	z-index: 5;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_2, $point_6) {
		top: 37.5%;
	}

	@include bp($point_6 - 1) {
		top: 24.6%;
	}

	&.featured_recipe__button--prev_mod {
		left: -2rem;
		transform: rotate(-180deg) translateY(-50%);
	}

	&.featured_recipe__button--next_mod {
		right: -2rem;
		transform: translateY(50%);
	}
}